import TileLayer from "ol/layer/Tile"
import TileArcGISRest from 'ol/source/TileArcGISRest'
import OSM from "ol/source/OSM"
import XYZ from 'ol/source/XYZ'
import BingMaps from 'ol/source/BingMaps'
// import a from "./map_瓦片：谷歌/tiles"

let maptype = 0    //0表示部署的离线瓦片地图，1表示OSM,2表示使用Arcgis在线午夜蓝地图服务

var streetmap = function () {
    var maplayer = null;
    switch (maptype) {
        // 离线地图
        case 0:
            maplayer = new TileLayer({
                source: new XYZ({
                    url: 'http://127.0.0.1:9090/map/Rectangle-5/tiles/{z}/{x}/{y}.png'
                    // url: 'http://127.0.0.1:9090/map/shenzheng_2_14/tiles/8/209/47.png'
                })
            })
            break;
        case 1:
            // 创建一个使用Open Street Map地图源的瓦片图层
            maplayer = new TileLayer({
                source: new OSM()
            })
            break;
        case 2:
            maplayer = new TileLayer({
                source: new TileArcGISRest({
                    url: 'https://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineCommunity/MapServer'
                })
            })
            break;
        // Bing地图
        case 3:
            // 创建图层 对象
            var styles = [
                'Road',
                'RoadOnDemand',
                'Aerial',
                'AerialWithLabels',
                'collinsBart',
                'ordnanceSurvey'
            ];
            var layers = [];
            var i, ii;
            for (i = 0, ii = styles.length; i < ii; ++i) {
                maplayer = layers.push(new TileLayer({
                    visible: false,
                    preload: Infinity,
                    source: new BingMaps({
                        key: 'Aqcwj7BiTxT9aSixmXgEAqP8GmZlRVSSGJLjowkE-oxH15bEMD1x5P6PsdAXk5wS&callback=loadMapScenario',
                        imagerySet: styles[i]
                        // use maxZoom 19 to see stretched tiles instead of the BingMaps
                        // "no photos at this zoom level" tiles
                        // maxZoom: 19
                    })
                }));
            }
            break;
    }
    return [maplayer];
}

var mapconfig = {
    // x: 114.054204,   
    // y: 22.554063,
    // zoom: 8,   
    x: -6655.5402445057125,
    y: 6709968.258934638,
    zoom: 13,
    streetmap: streetmap
};

export default mapconfig




