<template>
  <div id="app" class>
    <!-- 导航栏 -->
    <glo></glo>
    <!-- 历史数据 -->
    <span align="center">
      <h3>{{$t('details.Historical_data')}}</h3>
    </span>
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <div class="container">
        <div class="select_box" style="float:left">
          <el-select
            v-model="item"
            @change="eq_name"
            :placeholder="$t('historyData.Select_device')"
          >
            <el-option v-for="(item, index) in site_list" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </div>
        <div class="time_box" style="float:left">
          <el-date-picker
            hidden-xs
            v-model="value2"
            type="daterange"
            align="right"
            @change="get_time"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
            :range-separator="$t('historyData.to')"
            :start-placeholder="$t('historyData.start_date')"
            :end-placeholder="$t('historyData.End_date')"
            :picker-options="pickerOptions"
          ></el-date-picker>
        </div>
        <div class="time_mt_box hidden-sm hidden-md hidden-lg" style="float:left">手机上时间选择</div>
        <button
          class="btn btn-default"
          type="submit"
          style="font-size:14px; margin:0px 1px; padding:9px"
          @click="queryEquipment"
        >{{$t('historyData.Inquire')}}</button>
      </div>
    </div>

    <!-- 历史数据表 -->
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <div class="container table-responsive" style="max-height:150px;overflow-y:auto;">
        <table class="table table-striped table-bordered table-hover table-condensed scrolltable">
          <thead>
            <tr>
              <th>{{$t('historyData.device')}}</th>
              <th>{{$t('historyData.name')}}</th>
              <th>{{$t('historyData.Information_value')}}</th>
              <th>{{$t('historyData.unit')}}</th>
              <th>{{$t('historyData.content')}}</th>
              <th>{{$t('historyData.Acquisition_time')}}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in equipment_details_list" :key="index">
              <td>{{ item.equipment }}</td>
              <td>{{ item.name }}</td>
              <td>{{ item.sigid }}</td>
              <td>{{ item.unit }}</td>
              <td>{{ item.float_data }}</td>
              <td>{{ item.data_time }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- 历史数曲线 -->
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <div ref="myEchart" style="height:300px; width:100%;"></div>
    </div>
    <!-- 底部信息栏 -->
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <globar></globar>
    </div>
  </div>
</template>
<script>
import echarts from "echarts";
import { historyData, selectEquipment } from "../Api/api";
export default {
  // 父组件通过 props 向下传递数据给子组件；子组件通过 events 给父组件发送消息。
  props: {},
  created() {
    console.log(this.$route.query);
    this.selectEquip();
  },
  // 如果要通过某些插件操作页面上的DOM节点，最早要在mounted中进行，只要执行完了mounted就表示整个Vue实例已经初始化完毕了，此时组件已经脱离了创建阶段进入到了运行阶段了。
  mounted() {
    this.queryEquipment();
    this.initChart();
  },
  // 当执行到beforeDestory的时候，实例身上所有的data和methods，以及 过滤器/指令....还处于可用状态，只有到destroyed函数时候，组件已经被完全销毁了。
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  data() {
    return {
      item: "UPS", //下拉框的属性值
      starttime: "2019-06-21 12:02:01",
      endtime: "2019-09-21 12:02:01",

      chart: null,
      site_list: [],

      pickerOptions: {
        shortcuts: [
          {
            text: this.$t("echart.Last_week"),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: this.$t("echart.Last_month"),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: this.$t("echart.Last_three_months"),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value2: "",
      //   ----------------------------以上是日期选择器--------------------------
      equipment_details_list: []
    };
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$refs.myEchart);
      // 把配置和数据放这里
      this.chart.setOption({
        baseOption: {
          title: {
            text: this.$t("historyData.Historical_data_line_graph")
          },
          tooltip: {
            trigger: "axis"
          },
          legend: {
            data: ["UPS", "空调", "电表", "温湿度", "门状态"]
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "3%",
            containLabel: true
          },
          toolbox: {
            feature: {
              saveAsImage: {}
            }
          },
          xAxis: {
            type: "category",
            boundaryGap: false,
            data: [
              this.$t("echart.Monday"),
              "周二",
              "周三",
              "周四",
              "周五",
              "周六",
              "周日"
            ]
          },
          yAxis: {
            type: "value"
          },
          series: [
            {
              name: "门状态",
              type: "line",
              stack: "总量",
              data: [120, 132, 101, 134, 90, 230, 210]
            },
            {
              name: "UPS",
              type: "line",
              stack: "总量",
              data: [220, 182, 191, 234, 290, 330, 310]
            },
            {
              name: "空调",
              type: "line",
              stack: "总量",
              data: [150, 232, 201, 154, 190, 330, 410]
            },
            {
              name: "电表",
              type: "line",
              stack: "总量",
              data: [320, 332, 301, 334, 390, 330, 320]
            },
            {
              name: "温湿度",
              type: "line",
              stack: "总量",
              data: [820, 932, 901, 934, 1290, 1330, 1320]
            }
          ]
        },
        media: [
          //这里定义media query的逐条规则
          {
            query: { maxWidth: 768 },
            option: {
              legend: {
                right: 10, // legend 放置在右侧中间。
                top: "15%",
                orient: "vertical" // 纵向布局。
              },
              series: [
                {
                  center: ["50%", "30%"]
                },
                {
                  center: ["50%", "75%"]
                }
              ]
            }
          },
          {
            query: { minWidth: 768, maxWidth: 992 },
            option: {
              legend: {
                top: "center",
                top: "5%"
                // orient: "vertical" // 纵向布局。
              },
              series: [
                {
                  center: ["50%", "75%"]
                }
              ]
            }
          },
          {
            query: { minWidth: 992, maxWidth: 1200 },
            option: {
              legend: {
                top: "center",
                top: "5%"
                // orient: "vertical" // 纵向布局。
              },
              series: [
                {
                  center: ["100%", "100%"]
                }
              ]
            }
          }
        ]
      });
    },
    // 获取下拉框选择器(设备名称)
    eq_name(value) {
      this.item = value;
      // console.log(this.item);
    },
    // 获取时间选择器的值
    get_time(value) {
      this.starttime = value[0];
      this.endtime = value[1];
      // console.log( this.starttime);
    },
    // get请求用来查询设备列表（site_list）
    selectEquip() {
      selectEquipment().then(data => {
        let redata = JSON.parse(data.data); // JSON.parse() 方法用于将一个 JSON 字符串转换为对象
        this.site_list = redata;
        // console.log(this.site_list);
      });
    },
    // 发送post请求查询信息
    queryEquipment() {
      let Params = {
        ip:this.$route.query.ip,
        item: this.item,
        starttime: this.starttime,
        endtime: this.endtime
        // Date().toLocaleString()
      };
      historyData(Params).then(data => {
        this.equipment_details_list = data.data;
        // console.log(this.equipment_details_list);
      });
    }
  }
};
</script>
<style>
</style>



