<template>
  <div id="app" class="container">
    <!-- 导航条 -->
    <nav class="navbar navbar-default attom_nav">
      <div class="container-fluid">
        <!-- 移动端特有的展示效果 -->

        <div class="navbar-header">
          <!-- navbar-toggle collapsed   collapsed（手风琴）：在移动端会有手风琴效果 -->
          <!-- data-toggle="collapse" 手风琴组件 -->
          <!-- data-target  说明控制哪一个导航条的   data开头的都是自定义属性    -->
          <button
            type="button"
            class="navbar-toggle collapsed"
            data-toggle="collapse"
            data-target="#bs-example-navbar-collapse-1"
            aria-expanded="false"
          >
            <!-- sr-only 屏幕阅读器 -->
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <a class="navbar-brand motta_logo" href="#">
            <img src="../assets/ATTOM01.jpg" class="img-responsive center-block" />
          </a>
        </div>

        <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
          <ul class="nav navbar-nav" ref="li_id">
            <!-- <li
              :class="{active:activeClass === index}"
              v-for="(item, index) in nav_list"
              :key="index"
              @click="clickId(index, item.path)"
            >
              <a>{{ item.title }}</a>
            </li>-->
            <li class="dropdown">
              <router-link :to="{name:'Main'}" target="_self">{{$t("nav.Home")}}</router-link>
            </li>
            <li class="dropdown">
              <router-link :to="{name:'Manage'}" target="_self">{{$t("nav.authority_management")}}</router-link>
            </li>
            <li class="dropdown">
              <a
                href="#"
                class="dropdown-toggle"
                data-toggle="dropdown"
                role="button"
                aria-haspopup="true"
                aria-expanded="false"
              >
                {{$t('nav.Site_details')}}
                <span class="caret"></span>
              </a>
              <ul class="dropdown-menu">
                <li v-for="(item, index) in divices_list" :key="index">
                  <!-- <a href="#/details?v=1">{{item.address}}</a> -->
                  <!-- <router-view></router-view> -->
                  <router-link
                    :to="{name:'Details', query:{ip:item.ip,name:item.address,}}"
                  >{{item.address}}</router-link>
                </li>
              </ul>
            </li>
          </ul>
          <form class="navbar-form navbar-left hidden-sm">
            <!-- 输入框实现候选关键字功能 -->
            <el-select
              v-model="value"
              filterable
              remote
              reserve-keyword
              :placeholder="$t('nav.Please_enter_keyword')"
              :remote-method="remoteMethod"
              :loading="loading"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
            <!-- <div class="form-group">
              <input type="text" class="form-control" placeholder="请输入站点" />
            </div>-->
            <button
              type="submit"
              class="btn btn-default"
              @click="selectAddress"
            >{{$t('nav.search_for')}}</button>
          </form>

          <div class="nav navbar-nav"></div>
          <!-- ---------------------个人中心------------------------------- -->
          <ul class="nav navbar-nav navbar-right">
            <!-- 中英文切换 -->
            <li>
              <a>
                <el-switch
                  v-model="value1"
                  active-text="简体"
                  inactive-text="EN"
                  :change="switchLang()"
                ></el-switch>
              </a>
            </li>
            <li class="dropdown">
              <a
                href="#"
                class="dropdown-toggle"
                data-toggle="dropdown"
                role="button"
                aria-haspopup="true"
                aria-expanded="false"
              >
                {{$t('nav.Personal_center')}}
                <span class="caret"></span>
              </a>
              <ul class="dropdown-menu">
                <li>
                  <a href="#">{{$t('nav.My_message')}}</a>
                </li>
                <li>
                  <a href="#">{{$t('nav.Setting')}}</a>
                </li>
                <li role="separator" class="divider"></li>
                <li>
                  <a href="/">{{$t('nav.drop_out')}}</a>
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <!-- /.navbar-collapse -->
      </div>
      <!-- /.container-fluid -->
    </nav>
  </div>
</template>

<script>
import { newUser_address } from "../Api/api";
import { constants } from "fs";
export default {
  data() {
    return {
      //中英文切换
      value1: false,
      // 1.输入框候选关键字
      options: [],
      value: [],
      label: "",
      list: [],
      loading: false,
      states: [],
      // 站点列表
      divices_list: []
    };
  },
  created() {
    // 进入页面获取站点数据
    this.get_address();
    // 根据本地存储判断中英文界面
    if (localStorage.getItem("lang" == "cn")) {
      this.value1 = true;
    } else {
      if (this.$i18n.locale == "cn") {
        this.value1 = true;
      } else {
        this.value1 = false;
      }
    }
  },
  mounted() {},
  methods: {
    //0.中英文切换
    switchLang() {
      if (this.value1 == true) {
        this.$i18n.locale = "cn";
        localStorage.setItem("lang", "cn"); //考虑刷新操作，  存入localStorage
        // localStorage.setItem("value1", true);
      } else {
        this.$i18n.locale = "en";
        localStorage.setItem("lang", "en");
        // localStorage.setItem("value1", false);
      }
    },
    // 1.输入框候选关键字
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            // console.log(item.label.toLowerCase().indexOf(query.toLowerCase()))
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
          // console.log(this.value);
        }, 200);
      } else {
        this.options = [];
      }
    },
    // 点击搜索切换路由
    selectAddress() {
      if (this.value) {
        // console.log(this.list);
        // console.log(this.value);
        let name = {};
        name = this.list.find(item => {
          return item.value === this.value;
        });
        // console.log(name.label);
        this.$router.push({
          path: "/details",
          name: "Details",
          query: {
            ip: name.value,
            name: name.label
          }
        });
      } else {
        alert("请输入站点");
      }
    },
    // 获取站点列表
    get_address() {
      // 新建用户时，会查询站点，复用这个请求 newUser_address
      newUser_address().then(data => {
        this.divices_list = data.data;
        // console.log(this.divices_list);
        // data.data.forEach(element => {
        //   // 传过来的对象，只要站点名称的数据
        //   this.states.push(element.ip,element.address);
        // });
        // console.log(this.states)
        // 1.输入框候选关键字 提前在请求时候就要把这个赋值加上，不然数据加载不上去
        this.list = this.divices_list.map(item => {
          return { value: item.ip, label: item.address };
        });
        // console.log(this.list);
      });
    }
  },
  watch: {
    // "$route"() {
    //   let path = this.$route.path;
    //   this.checkrouter(path);
    // }
  }
};
</script>

<style scoped>
#app {
  margin-top: 10px;
}
</style>


