import Vue from "vue"
import Vuex from "vuex"
Vue.use(Vuex)

const module_warninglist1 = {
    state: {
        // 严重告警
        warning_list1: ""
    },
    mutations: {
        // 存储
        showTimeList1(state, warning_list1) {
            state.warning_list1 = warning_list1
        }
    },
    getters: {
        // getters是store 的计算属性
        get_showTimeList1: state => {
            return state.warning_list1
        }
    }
}

const module_warninglist3 = {
    state: {
        // 一般告警
        warning_list3: ""
    },
    mutations: {
        // 存储
        showTimeList3(state, warning_list3) {
            state.warning_list3 = warning_list3
        }
    },
    getters: {
        // getters是store 的计算属性
        get_showTimeList3: state => {
            return state.warning_list3
        }
    }
}

const module_warninglist2 = {
    state: {
        // 环境的模块实时数据
        warning_list2: ""
    },
    mutations: {
        // 存储
        showTimeList2(state, warning_list2) {
            state.warning_list2 = warning_list2
        }
    },
    // getters是store 的计算属性
    getters: {
        // 可以使用过滤器
        get_showTimeList2: state => {
            return state.warning_list2
        }
    }
}

const module_bar = {
    state: {
        // 环境的模块实时数据
        bar_list: ""
    },
    mutations: {
        // 存储
        bar_list(state, bar_list) {
            state.bar_list = bar_list
        }
    },
    // getters是store 的计算属性
    getters: {
        // 可以使用过滤器
        get_bar_list: state => {
            return state.bar_list
        }
    }
}
const store = new Vuex.Store({
    modules: {
        warning_list1: module_warninglist1,
        warning_list3: module_warninglist3,
        warning_list2: module_warninglist2,
        bar: module_bar


    }
})
export default store


