<template>
  <div id="app" class>
    <!-- 导航栏 -->
    <glo></glo>
    <!-- 历史告警 -->
    <span align="center">
      <h3>{{$t('details.Historical_alarm')}}</h3>
    </span>
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <div class="container">
        <div class="select_box" style="float:left">
          <el-select v-model="id" @change="eq_name" :placeholder="$t('historyData.Select_device')">
            <el-option v-for="(item, index) in site_list" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </div>
        <div class="time_box hidden-xs" style="float:left">
          <el-date-picker
            hidden-xs
            v-model="value2"
            type="daterange"
            align="right"
            unlink-panels
            :range-separator="$t('historyData.to')"
            :start-placeholder="$t('historyData.start_date')"
            :end-placeholder="$t('historyData.End_date')"
            :picker-options="pickerOptions"
            @change="get_time"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </div>
        <div class="time_mt_box hidden-sm hidden-md hidden-lg" style="float:left">手机上时间选择</div>
        <button
          class="btn btn-default"
          type="submit"
          style="font-size:14px; margin:0px 1px; padding:9px"
          @click="queryEquipment "
        >{{$t('historyData.Inquire')}}</button>
      </div>
    </div>

    <!-- 历史数据表 -->
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <div class="container table-responsive" style="max-height:300px;overflow-y:auto;">
        <table class="table table-striped table-bordered table-hover table-condensed scrolltable">
          <thead>
            <tr>
              <th>{{$t('historyData.device')}}</th>
              <th>{{$t('historyData.Information_value')}}</th>
              <th>{{$t('historyData.unit')}}</th>
              <th>{{$t('warning.Alarm_level')}}</th>
              <th>{{$t('historyData.content')}}</th>
              <th>{{$t('warning.Starting_time')}}</th>
              <th>{{$t('warning.End_Time')}}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in warning_list" :key="index">
              <td>{{ item.equipment }}</td>
              <td>{{ item.information }}</td>
              <td>{{ item.unit }}</td>
              <td>{{ item.warn_level }}</td>
              <td>{{ item.warn_text }}</td>
              <td>{{ item.start_time }}</td>
              <td>{{ item.end_time }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- 底部信息栏 -->
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <globar></globar>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { historyAlarm, selectEquipment } from "../Api/api";
export default {
  data() {
    return {
      // 接受vuex传过来的值
      ip: "",
      //下拉框的属性值
      item: "UPS",
      starttime: "2019-06-21 12:02:01",
      endtime: "2019-09-21 12:02:01",
      site_list: [],
      id: "",
      //   ----------------------------以上是下拉框选择--------------------------
      pickerOptions: {
        shortcuts: [
          {
            text: this.$t("echart.Last_week"),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: this.$t("echart.Last_month"),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: this.$t("echart.Last_three_months"),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value2: "",
      //   ----------------------------以上是日期选择器--------------------------
      warning_list: []
    };
  },
  created() {
    // 获取查询字符串的ip数据
    this.ip = this.$route.query.ip;
    // console.log(this.ip);
    //get请求用来查询 设备列表，用于下拉框选择
    this.selectEquip();
  },
  mounted() {
    //查询列表
    this.queryEquipment();
  },
  methods: {
    // 获取下拉框选择器(设备名称)
    eq_name(value) {
      this.item = value;
      // console.log(this.item);
    },
    // 获取时间选择器的值
    get_time(value) {
      this.starttime = value[0];
      this.endtime = value[1];
      // console.log( this.starttime);
    },
    // get请求用来查询 设备列表（site_list）
    selectEquip() {
      // { params: { ip: this.ip } }这里面的params还不能写错
      let params = { params: { ip: this.ip } };
      console.log(params);
      selectEquipment(params).then(data => {
        let redata = JSON.parse(data.data); // JSON.parse() 方法用于将一个 JSON 字符串转换为对象
        this.site_list = redata;
        console.log(this.site_list);
      });
    },
    // 发送post请求查询信息
    queryEquipment() {
      let Params = {
        ip: this.$route.query.ip,
        item: this.item,
        starttime: this.starttime,
        endtime: this.endtime
        // Date().toLocaleString()
      };
      historyAlarm(Params).then(data => {
        this.warning_list = data.data;
        // console.log(this.equipment_details_list);
      });
    }
  },
  computed: {
    // ...mapGetters(["get_bar_list"])
  },
  watch: {}
};
</script>
<style>
</style>



