<template>
  <div>
    <label>
      站点地址：
      <input v-model="keyword" />
    </label>
    <button type="button">添加告警图标</button>
    <baidu-map
      class="map"
      :center="center"
      :zoom="zoom"
      :scroll-wheel-zoom="true"
      @ready="handler"
      @click="getClickInfo"
    >
      <!-- //地理位置的搜索功能 -->
      <bm-local-search
        :keyword="keyword"
        :auto-viewport="true"
        :panel="false"
        :selectFirstResult="true"
      ></bm-local-search>

      <!-- <bml-marker-clusterer :averageCenter="true"> -->
      <!-- <bm-marker @click="infoWindowOpen2" v-for="(marker, index) in markers" :key="index" :position="{lng: marker.lng, lat: marker.lat}"></bm-marker> -->
      <!-- </bml-marker-clusterer> -->

      <!-- 1.比例尺控件 -->
      <!-- <bm-scale anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-scale> -->
      <!-- 2.在右上角加入缩放控件 -->
      <!-- <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation> -->
      <!-- 3.在地图右下角加入定位控件 -->
      <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true"></bm-geolocation>
      <!-- 4.在地图左上角加入城市切换控件 -->
      <!-- 城市列表控件仅在地图视图尺寸足够大时生效 所以设置style="height: 500px" -->
      <!-- <bm-city-list anchor="BMAP_ANCHOR_TOP_LEFT"></bm-city-list> -->
    </baidu-map>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //   markers,
      baidumapSwitch: false,
      center: { lng: 0, lat: 0 },
      zoom: 0,
      keyword: ""
    };
  },
  //   第三方组件是对基于百度地图 JS API 开发的开源库的封装，该分类中的组件不参与全局注册，
  //   请根据使用频度需求自行进行全局 / 局部注册。
  methods: {
    handler({ BMap, map }) {
      // console.log(BMap, map);
      this.center.lng = 100.404;
      this.center.lat = 39.915;
      this.zoom = 1;
    },
    getClickInfo(e) {
      // console.log(e);
      // console.log(e.point.lng);
      // console.log(e.point.lat);
      this.center.lng = e.point.lng;
      this.center.lat = e.point.lat;
    },
    
    // //双向绑定经纬度以及缩放尺寸
    // syncCenterAndZoom(e) {
    //   const { lng, lat } = e.target.getCenter();
    //   this.center.lng = lng;
    //   this.center.lat = lat;
    //   // this.zoom = e.target.getZoom();
    // },
    // //经纬度同步
    // baidumap() {
    //   this.baiduDevicelocationx = this.center.lng;
    //   this.baiduDevicelocationy = this.center.lat;
    // }
  },
  components: {}
};
// 1. BaiduMap 组件容器本身是一个空的块级元素，如果容器不定义高度，百度地图将渲染在一个高度为 0 不可见的容器内。
// 没有设置 center 和 zoom 属性的地图组件是不进行地图渲染的。当center 属性为合法地名字符串时例外，
// 因为百度地图会根据地名自动调整 zoom 的值。
// 2. 由于百度地图 JS API 只有 JSONP 一种加载方式，因此 BaiduMap 组件及其所有子组件的渲染只能是异步的。
// 因此，请使用在组件的 ready 事件来执行地图 API 加载完毕后才能执行的代码，
// 不要试图在 vue 自身的生命周期中调用 BMap 类，更不要在这些时机修改 model 层。
</script>

<style>
/* 去掉百度地图的logo */
.anchorBL {
  display: none;
}
.map {
  width: 100%;
  height: 300px;
}
</style>



