// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// -----------------------------------------------

import _ from "lodash"
// import $ from 'jquery'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

import Vuex from 'vuex'
import store from "./store/store"
Vue.use(Vuex)

import Axios from 'axios'
Axios.defaults.withCredentials = true;//让ajax携带cookie
Vue.prototype.$axios = Axios

// 安装百度地图组件
import BaiduMap from 'vue-baidu-map'
Vue.use(BaiduMap, {
  ak: 'w9mz4iNrGzzGmBSToSlZOGX5AIwplyeC'
})

import BmlMarkerClusterer from 'vue-baidu-map'
Vue.component('bml-marker-clusterer', BmlMarkerClusterer)

//中英文切换
import i18n from './i18n/i18n'
Vue.use(i18n)

// 自定义全局组件
import Global_com from './components/Global_components'
import Global_bar from './components/Global_information_bar'
import Global_echarts from './components/Global_echarts'
import Mshow from './components/Mshow'
import Baidumap from './components/BaiduMap'
Vue.component("glo", Global_com)
Vue.component("globar", Global_bar)
Vue.component("gloecharts", Global_echarts)
Vue.component("globalmap", Mshow)
Vue.component("globalbaidu", Baidumap)
// -----------------------------------------------
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  components: { App },
  template: '<App/>',

})



