import axios from 'axios';

let base = 'http://192.168.208.144:8000';

// export const ws_url = '192.168.208.141:8000'

export const requestLogin = params => { return axios.post(`${base}/authorizations/`, params).then(res => res.data); };

// 六、查询设备列表(Details)
export const selectEquipment = params => { return axios.get(`${base}/selectequipment/`, params); };

// 七、历史数据列表(Historical_data)
export const historyData = params => { return axios.post(`${base}/historydata/`, params); };

// 八、历史告警
export const historyAlarm = params => { return axios.post(`${base}/historyalarm/`, params); };

// 九、新增用户
export const newUser = params => { return axios.post(`${base}/newuser/`, params); };

//十、查询现有站点
export const newUser_address = params => { return axios.get(`${base}/newuser_address/`, params); };

// 十一、新增站点
export const newAddress = params => { return axios.post(`${base}/newaddress/`, params); };

//十二、查询现有用户
export const newAddress_user = params => { return axios.get(`${base}/newaddress_user/`, params); };

//十三、删除现有用户
export const onDelete_user = params => { return axios.post(`${base}/ondelete_user/`, params); };

//十四、删除现有站点
export const onDelete_address = params => { return axios.post(`${base}/ondelete_address/`, params); };

//十五、修改现有用户
export const onModify_user_put = params => { return axios.post(`${base}/onmodify_user/`, params); };

//十六、修改现有站点
export const onModify_address_put = params => { return axios.post(`${base}/onmodify_address/`, params); };

//十七、告警配置
export const Warning_config_put = params => { return axios.post(`${base}/warning_config_put/`, params); };

//十八、查询告警配置
export const Warning_config_post = params => { return axios.post(`${base}/warning_config_post/`, params); };

//十九、页脚详情信息查询
export const Bar_get = params => { return axios.get(`${base}/bar_get/`, params); };





