<template>
  <div id="app" class="container">
    <!-- 导航条 -->
    <glo></glo>
    <!-- 地图 -->
    <div class="container-fluid map">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
          <div class="bg-success" align="center">
            <h3>{{$t('main.map_title')}}</h3>
          </div>
          <!-- <div class="jumbotron"> -->
          <!-- 地图全局组件 -->
          <!-- <globalmap></globalmap> -->
          <globalbaidu></globalbaidu>

          <!-- </div> -->
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
          <div style="width:100%" align="center">
            <button
              type="button"
              class="btn btn-warning"
            >{{$t('main.General_alarm')}}（{{this.warning_list1.length}}{{$t('main.One')}}）</button>
            <!-- Indicates a dangerous or potentially negative action -->
            <button
              type="button"
              class="btn btn-danger"
            >{{$t('main.Critical_alarm')}}（{{this.warning_list.length}}{{$t('main.One')}}）</button>
          </div>
          <!-- 严重告警列表 -->
          <h3 class="text-center">{{$t('main.Critical_alarm_list')}}</h3>
          <div class="table" style="overflow-x:scroll; border:0px; height:300px;">
            <table class="table table-hover" style="overflow-x:scroll;">
              <thead>
                <tr>
                  <th>{{$t('main.Site')}}</th>
                  <th>{{$t('main.device')}}</th>
                  <th>{{$t('main.name')}}</th>
                  <th>{{$t('main.content')}}</th>
                  <th>{{$t('main.time')}}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in warning_list" :key="index">
                  <td>{{ item.divice_id }}</td>
                  <td>{{ item.alarm_model }}</td>
                  <td>{{ item.alarm_divice }}</td>
                  <td>{{ item.alarm_data }}</td>
                  <td>{{ item.alarm_time }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      websock: null,
      warning_list: {},
      warning_list1: {},
      warning_list2: {},
      warning_list3: {},
    };
  },

  created() {
    this.initWebSocket();
  },
  destroyed() {
    // this.websock.close(); //离开路由之后断开websocket连接
  },
  mounted() {},
  methods: {
    initWebSocket() {
      //初始化weosocket
      const wsUrl = "ws://192.168.208.144d:8000/echo/";
      // 创建websocket实例
      this.websock = new WebSocket(wsUrl);
      // 一点建立了连接，websocket实例中就会触发open事件，通过send方法发送数据
      this.websock.onopen = this.websocketonopen;
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketonclose;
    },
    websocketonopen() {
      let actions = "I'm is websocket";
      // 前端传给后端都是字符串类型 JSON.stringify将 JavaScript 对象转换为字符串
      this.websock.send(JSON.stringify(actions));
    },
    websocketonerror(e) {
      //连接失败
      // alert("服务器连接错误...");
      console.log("服务器没有数据...");
    },
    websocketonmessage(e) {
      //数据接收
      let redata = JSON.parse(e.data); //一般传输的是字符串类型 JSON.parse() 方法将数据转换为 JavaScript 对象，
      this.warning_list = redata.严重告警;
      this.warning_list1 = redata.一般告警;
      this.warning_list2 = redata.data_dict;
      // 保存数据到公共组件（vuex）
      // concat()合并列表
      this.warning_list3 = this.warning_list.concat(this.warning_list1);
      // this.$store.commit("showTimeList1", this.warning_list1);
      this.$store.commit("showTimeList3", this.warning_list3);
      this.$store.commit("showTimeList2", this.warning_list2);
      // console.log(this.$store.state.warning_list2);
    },
    websocketonclose() {
      // 连接中断 1秒后重新连接
      setTimeout(() => {
        this.initWebSocket();
      }, 1000);
    }
  }
};
</script>

<style scoped>
#app {
  margin-top: 10px;
}
</style>


