<template>
  <div :style="{width:childWidth, height:childHeight}" class="line-wrap">
    <div id="lines" :style="{width:childWidth, height:childHeight}" ref="ebox"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: {
    // 父组件通过 props 向下传递数据给子组件；子组件通过 events 给父组件发送消息。
    lineData: Object,
    isClick: Boolean
  },
  watch: {
    // 在watch中监听高度变化，然后更新视图，由于父组件直接传过来的数据不能直接使用 需要通过变量来接收
    lineData: {
      // deep的意思就是深入观察，监听器会一层层的往下遍历，给对象的所有属性都加上这个监听器，但是这样性能开销就会非常大了，任何修改obj里面任何一个属性都会触发这个监听器里的 handler。
      deep: true,
      handler(newValue, oldValue) {
        this.childWidth = newValue.divOffset.eWidth;
        this.childHeight = newValue.divOffset.eHeight;
        this.ininChart(newValue.options, this.isClick);
      }
    }
  },
  methods: {
    ininChart(options, isClick) {
      const _this = this;
      const lineDiv = this.$refs.ebox;
      linesDiv.style.width = this.childWidth;
      linesDiv.style.height = this.childHeight;

      this.$nextTick(() => {
        //nextTick在下次 DOM 更新循环结束之后执行延迟回调。在修改数据之后立即使用这个方法，获取更新后的 DOM。
        this.lineCharts = this.$echarts.init(linesDiv);
        this.lineCharts.setOption(options);
        // 支持点击事件
        if (isClick) {
          this.lineCharts.on("click", params => {
            //jQuery中on() 方法在被选元素及子元素上添加一个或多个事件处理程序。
            const index = params.dataIndex;
            this.$emit("clickFn", index);
          });
        }
        this.lineCharts.resize(); //jQuery中 当调整浏览器窗口的大小时，发生 resize 事件。resize() 方法触发 resize 事件，或规定当发生 resize 事件时运行的函数。
      });
    }
  },
  // 离开页面注销echart
  destroyed() {
    if (this.lineCharts !== "") {
      this.lineCharts.dispose();
    }
  }
};
</script>
<style>
</style>



