<template>
  <div id="app" class="container">
    <!-- 导航条 -->
    <glo></glo>
    <!-- 用户 站点 -->
    <div class="container-fluid user">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
          <button
            class="btn btn-primary btn-lg"
            data-toggle="modal"
            data-target="#myModal"
          >{{$t('manage.New_users')}}</button>
          <div
            class="modal fade"
            id="myModal"
            tabindex="-1"
            role="dialog"
            aria-labelledby="myModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog">
              <!-- 外面二个盒子是做弹窗功能 -->
              <div class="modal-content">
                <div class="modal-header">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >&times;</button>
                  <h4 class="modal-title" id="myModalLabel">{{$t('manage.New_users')}}</h4>
                </div>
                <div class="modal-body">
                  <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item :label="$t('manage.Privilege_level')">
                      <el-select
                        v-model="form.level"
                        :placeholder="$t('manage.Please_select_permission')"
                      >
                        <el-option :label="$t('manage.Super_administrator')" value="1"></el-option>
                        <el-option :label="$t('manage.General_administrator')" value="0"></el-option>
                      </el-select>
                    </el-form-item>
                    <!-- 站点选择需要后端传输数据 -->
                    <el-form-item :label="$t('manage.Responsible_site')">
                      <el-select
                        v-model="form.site"
                        multiple
                        filterable
                        allow-create
                        default-first-option
                        :placeholder="$t('manage.Please_select_site')"
                      >
                        <el-option
                          v-for="item in site_list"
                          :key="item.id"
                          :label="item.address"
                          :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item :label="$t('manage.user_name')">
                      <el-input v-model="form.user"></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('manage.user_password')">
                      <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('manage.mailbox')">
                      <el-input v-model="form.email"></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('manage.phone')">
                      <el-input v-model="form.phone"></el-input>
                    </el-form-item>
                  </el-form>
                </div>
                <div class="modal-footer">
                  <button
                    type="button"
                    class="btn btn-default"
                    data-dismiss="modal"
                  >{{$t('manage.shut_down')}}</button>
                  <button
                    type="button"
                    class="btn btn-primary"
                    @click="onSubmit"
                  >{{$t('manage.Submit_changes')}}</button>
                </div>
              </div>
            </div>
          </div>
          <!-- -----------------------以上是新增用户弹窗----------------------------- -->
          <div
            class="modal fade"
            id="modifyUser"
            tabindex="-1"
            role="dialog"
            aria-labelledby="myModalUser"
            aria-hidden="true"
          >
            <div class="modal-dialog">
              <!-- 外面二个盒子是做弹窗功能 -->
              <div class="modal-content">
                <div class="modal-header">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >&times;</button>
                  <h4 class="modal-title" id="myModalUser">{{$t('manage.Edit_user')}}:{{form.user}}</h4>
                </div>
                <div class="modal-body">
                  <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item :label="$t('manage.Privilege_level')">
                      <el-select
                        v-model="form.level"
                        :placeholder="$t('manage.Please_select_permission')"
                      >
                        <el-option :label="$t('manage.Super_administrator')" value="1"></el-option>
                        <el-option :label="$t('manage.General_administrator')" value="0"></el-option>
                      </el-select>
                    </el-form-item>
                    <!-- 站点选择需要后端传输数据 -->
                    <el-form-item :label="$t('manage.Responsible_site')">
                      <el-select
                        v-model="form.site"
                        multiple
                        filterable
                        allow-create
                        default-first-option
                        :placeholder="$t('manage.Please_select_site')"
                      >
                        <el-option
                          v-for="item in site_list"
                          :key="item.id"
                          :label="item.address"
                          :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item :label="$t('manage.mailbox')">
                      <el-input v-model="form.email"></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('manage.phone')">
                      <el-input v-model="form.phone"></el-input>
                    </el-form-item>
                  </el-form>
                </div>
                <div class="modal-footer">
                  <button
                    type="button"
                    class="btn btn-default"
                    data-dismiss="modal"
                  >{{$t('manage.shut_down')}}</button>
                  <button
                    type="button"
                    class="btn btn-primary"
                    @click="putUser"
                  >{{$t('manage.Submit_changes')}}</button>
                </div>
              </div>
            </div>
          </div>
          <!-- -----------------------以上是修改用户弹窗----------------------------- -->
          <ul class="list-group" style="height:200px; overflow-y:scroll;">
            <li
              class="list-group-item"
              ref="refUsers"
              :class="activeClass == index ? 'active' : ''"
              v-for="(user, index) in user_list"
              :key="user.id"
              @click="users(user, index)"
            >
              {{ user.user}}
              <a
                class="btn btn-default"
                style="float:right;line-height:50%"
                href="#"
                role="button"
                @click="onDelete_user(user.id, user.user)"
              >{{$t('manage.Delete')}}</a>
              <a
                class="btn btn-default"
                data-toggle="modal"
                data-target="#modifyUser"
                style="float:right;line-height:50%"
                href="#"
                role="button"
                @click="putUser1(user, user.user)"
              >{{$t('manage.Modify')}}</a>
            </li>
          </ul>
          <ul class="list-group">
            <li class="list-group-item" style="text-align:center;" :user-data="user_data">
              <label for="exampleInputEmail1">{{$t('manage.Privilege_level')}}：</label>
              {{user_data.level}}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.user_name')}}：</label>
              {{user_data.user}}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.Responsible_site')}}：</label>
              {{user_data.site}}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.mailbox')}}：</label>
              {{user_data.email}}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.phone')}}：</label>
              {{user_data.phone}}
              <br />
            </li>
          </ul>
        </div>
        <!-- -----------------------以上是用户列表显示----------------------------- -->
        <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
          <button
            class="btn btn-primary btn-lg"
            style="background-color:pink"
            data-toggle="modal"
            data-target="#myModal_1"
          >{{$t('manage.Add_site')}}</button>
          <div
            class="modal fade"
            id="myModal_1"
            tabindex="-1"
            role="dialog"
            aria-labelledby="myModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog">
              <div class="modal-content">
                <div class="modal-header">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >&times;</button>
                  <h4 class="modal-title" id="myModalLabel">{{$t('manage.Add_site')}}</h4>
                </div>
                <div class="modal-body">
                  <el-form ref="form" :model="form" label-width="80px">
                    <!-- 站点选择需要后端传输数据 -->
                    <el-form-item :label="$t('manage.Site_administrator')">
                      <el-select
                        v-model="form.addUser"
                        multiple
                        filterable
                        allow-create
                        default-first-option
                        :placeholder="$t('manage.Select_user')"
                      >
                        <el-option
                          v-for="item in user_list"
                          :key="item.id"
                          :label="item.user"
                          :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item :label="$t('manage.Site_name')">
                      <el-input v-model="form.address"></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('manage.Site_IP')">
                      <el-input v-model="form.ip"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="收件邮箱">
                      <el-input v-model="form.add_email"></el-input>
                    </el-form-item>
                    <el-form-item label="短信电话">
                      <el-input v-model="form.add_phone"></el-input>
                    </el-form-item>-->
                  </el-form>
                </div>
                <div class="modal-footer">
                  <button
                    type="button"
                    class="btn btn-default"
                    data-dismiss="modal"
                  >{{$t('manage.shut_down')}}</button>
                  <button
                    type="button"
                    class="btn btn-primary"
                    @click="onSubmit01"
                  >{{$t('manage.Submit_changes')}}</button>
                </div>
              </div>
            </div>
          </div>
          <!-- -----------------------以上是新增站点弹窗----------------------------- -->
          <div
            class="modal fade"
            id="myModalAddress"
            tabindex="-1"
            role="dialog"
            aria-labelledby="myModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog">
              <div class="modal-content">
                <div class="modal-header">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >&times;</button>
                  <h4 class="modal-title" id="myModalLabel">{{$t('manage.Site_IP')}}</h4>
                </div>
                <div class="modal-body">
                  <el-form ref="form" :model="form" label-width="80px">
                    <!-- 站点选择需要后端传输数据 -->
                    <el-form-item :label="$t('manage.Site_administrator')">
                      <el-select
                        v-model="form.addUser"
                        multiple
                        filterable
                        allow-create
                        default-first-option
                        :placeholder="$t('manage.Select_user')"
                      >
                        <el-option
                          v-for="item in user_list"
                          :key="item.id"
                          :label="item.user"
                          :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item :label="$t('manage.Site_name')">
                      <el-input v-model="form.address"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="收件邮箱">
                      <el-input v-model="form.add_email"></el-input>
                    </el-form-item>
                    <el-form-item label="短信电话">
                      <el-input v-model="form.add_phone"></el-input>
                    </el-form-item>-->
                  </el-form>
                </div>
                <div class="modal-footer">
                  <button
                    type="button"
                    class="btn btn-default"
                    data-dismiss="modal"
                  >{{$t('manage.shut_down')}}</button>
                  <button
                    type="button"
                    class="btn btn-primary"
                    @click="putAddress"
                  >{{$t('manage.Submit_changes')}}</button>
                </div>
              </div>
            </div>
          </div>
          <!-- -----------------------以上是修改站点弹窗----------------------------- -->

          <ul class="list-group" style="height:200px; overflow-y:scroll;">
            <li
              class="list-group-item"
              ref="refadd"
              :class="{active:activeClass_add == index}"
              v-for="(add, index) in site_list"
              :key="add.id"
              @click="adds(add, index)"
            >
              {{ add.address}}
              <a
                class="btn btn-default"
                style="float:right;line-height:50%"
                href="#"
                role="button"
                @click="onDelete_address(add.id,add.address)"
              >{{$t('manage.Delete')}}</a>
              <a
                class="btn btn-default"
                data-toggle="modal"
                data-target="#myModalAddress"
                style="float:right;line-height:50%"
                href="#"
                role="button"
                @click="onModify_add(add)"
              >{{$t('manage.Modify')}}</a>
            </li>
          </ul>
          <ul class="list-group" style="height:150px">
            <li class="list-group-item" style="text-align:center;" :addr-data="add_data">
              <br />
              <label for="exampleInputEmail1">{{$t('manage.Site_IP')}}：</label>
              {{add_data.ip}}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.Site_name')}}：</label>
              {{ add_data.address}}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.Site_administrator')}}：</label>
              {{ add_data.addUser}}
              <br />
              <!-- <label for="exampleInputEmail1"></label>
              {{ add_data.add_email}}-->
              <br />
            </li>
          </ul>
        </div>
        <!-- -----------------------以上是站点列表显示----------------------------- -->
        <div
          class="modal fade"
          id="myModalAddressConfig"
          tabindex="-1"
          role="dialog"
          aria-labelledby="myModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
                <h4 class="modal-title" id="myModalLabel">{{$t('manage.Alarm_configuration')}}</h4>
              </div>
              <div class="modal-body">
                <el-form ref="form" :model="warning_list" label-width="80px">
                  <el-form-item :label="$t('manage.server_address')">
                    <el-input v-model="warning_list.server_address"></el-input>
                  </el-form-item>
                  <el-form-item :label="$t('manage.Port_configuration')">
                    <el-input v-model="warning_list.port_config"></el-input>
                  </el-form-item>
                  <el-form-item :label="$t('manage.Outbox')">
                    <el-input v-model="warning_list.send_mail"></el-input>
                  </el-form-item>
                  <el-form-item :label="$t('manage.password')">
                    <el-input v-model="warning_list.password"></el-input>
                  </el-form-item>
                  <el-form-item :label="$t('manage.Telephone_port_number')">
                    <el-input v-model="warning_list.COM_phone"></el-input>
                  </el-form-item>
                  <el-form-item :label="$t('manage.other_settings')">
                    <el-input v-model="warning_list.other_config"></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <div class="modal-footer">
                <button
                  type="button"
                  class="btn btn-default"
                  data-dismiss="modal"
                >{{$t('manage.shut_down')}}</button>
                <button
                  type="button"
                  class="btn btn-primary"
                  @click="putAddressConfig"
                >{{$t('manage.Submit_changes')}}</button>
              </div>
            </div>
          </div>
        </div>
        <!-- -----------------------以上是配置弹窗----------------------------- -->
        <div
          class="col-xs-12 col-sm-12 col-md-12 col-lg-12"
          style="border: 1px solid #ddd; margin:10px 10px"
        >
          <h4>
            <button
              type="button"
              class="btn btn-default navbar-btn"
              data-toggle="modal"
              data-target="#myModalAddressConfig"
            >{{$t('manage.Alarm_configuration')}}</button>
          </h4>
          <div class="form-group" style="width:50%; float:left; overflow-x:hidden">
            <li class="list-group-item" style="border:0px;">
              <label for="exampleInputEmail1">{{$t('manage.server_address')}}：</label>
              {{ warning_list.server_address }}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.Port_configuration')}}：</label>
              {{ warning_list.port_config }}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.Outbox')}}：</label>
              {{ warning_list.send_mail }}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.password')}}：</label>
              {{ warning_list.password }}
            </li>
          </div>
          <div class="form-group" style="width:50%; float:right; overflow-x:hidden">
            <li class="list-group-item" style="border:0px;">
              <label for="exampleInputEmail1">{{$t('manage.Telephone_port_number')}}：</label>
              {{ warning_list.COM_phone }}
              <br />
              <label for="exampleInputEmail1">{{$t('manage.other_settings')}}：</label>
              {{ warning_list.other_config }}
            </li>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  newUser,
  newUser_address,
  onDelete_user,
  onModify_user_put
} from "../Api/api";
import {
  newAddress,
  newAddress_user,
  onDelete_address,
  onModify_address_put
} from "../Api/api";
import { Warning_config_put, Warning_config_post } from "../Api/api";
import { connect } from "net";
export default {
  inject: ["reload"], //插入全局方法
  data() {
    return {
      // 新增用户及站点
      form: {},
      // 站点列表
      site_list: [],
      // 用户列表
      user_list: [],
      activeClass: 0,
      activeClass_add: 0,

      user_data: {},
      add_data: {},
      // 底部配置信息
      warning_list: {}
      // 如何获取ip地址（逻辑是否正确，是否有用看后面的关联）
      // ip_address: "192.168.1.200"
    };
  },

  created() {
    // 初始化 关联的站点列表 和 关联的用户列表
    this.getAddressList();
    this.getUserList();
    // 查询告警配置信息
    this.get_addressconfig();
  },
  beforeMount() {},
  mounted() {
    // 登入界面选中第一行，并显示数据(需要等this.getUserList();发送完请求获取到数据后，再执行users方法)
    // this.users(this.user_list[0], this.activeClass);
    // this.adds(this.site_list[0], this.activeClass_add);
  },
  beforeUpdate() {
    //放到这里无法产生联动
  },
  methods: {
    // 新增用户 表单提交
    onSubmit() {
      var fromData = {
        level: this.form.level,
        site: this.form.site,
        user: this.form.user,
        password: this.form.password,
        email: this.form.email,
        phone: this.form.phone
      };
      newUser(fromData).then(data => {
        alert(data.data);
        this.reload();
        // 使用jquery代替bootstrap的 data-dismiss="modal"方法 执行完后，关闭模态框和遮罩层
        $("#myModal").modal("hide");
      });
    },
    // 获取站点列表
    getAddressList() {
      newUser_address().then(data => {
        this.site_list = data.data;
        // console.log(this.site_list);
        if (data.data !== undefined && data.data.length > 0) {
          // 作用：用户详情框默认显示第一条数据（选中状态根据active判断）
          this.add_data = this.site_list[0];
        } else {
          return null;
        }
      });
    },
    // 新增站点 表单提交
    onSubmit01() {
      var fromData01 = {
        addUser: this.form.addUser,
        address: this.form.address,
        ip: this.form.ip,
        add_email: this.form.add_email,
        add_phone: this.form.add_phone
      };
      // console.log(fromData01);
      newAddress(fromData01).then(data => {
        alert(data.data);
        this.reload();
        // 使用jquery代替bootstrap的 data-dismiss="modal"方法 执行完后，关闭模态框和遮罩层
        $("#myModal_1").modal("hide");
      });
    },
    // 获取用户列表
    getUserList() {
      newAddress_user().then(data => {
        // 得到一个列表 里面放着用户字典对象
        // console.log(data.data)
        this.user_list = data.data;
        if (data.data !== undefined && data.data.length > 0) {
          // 作用：用户详情框默认显示第一条数据（选中状态根据active判断）
          this.user_data = this.user_list[0];
        } else {
          return null;
        }
      });
    },
    // 显示用户详情
    users(user, index) {
      // 根据index索引来判断，显示选中的行
      this.activeClass = index;
      // 1.得到一个user_dict字典对象{email: ""，id: 1，level: "一般管理员"，phone: 1314141415，site: Array(3)，user: "chenjun"}
      // 2.将对象赋值给this.user_data
      this.user_data = user;
    },
    // 显示站点详情
    adds(add, index) {
      this.activeClass_add = index;
      this.add_data = add;
      // console.log(this.add_data);
    },
    // 删除用户
    onDelete_user(id, user) {
      var _user_obj = { user: user };
      onDelete_user(_user_obj).then(data => {
        // console.log(user_obj);
        alert(data.data);
        this.reload();
      });
    },
    // 删除站点
    onDelete_address(id, address) {
      var _address_obj = { id: id };
      // console.log(address_obj);
      onDelete_address(_address_obj).then(data => {
        // console.log(user_obj);
        alert(data.data);
        this.reload();
      });
    },
    // 修改用户数据时显示旧的数据(友好体验)
    putUser1(user_obj, user_name) {
      // 1.先把所有数据渲染上去
      this.form = user_obj;
      // 5.得到的id列表
      let add_list = [];
      //2.遍历数组
      user_obj.site.forEach(element => {
        let add_obj = {};
        //3.在另外的数组中找到对应的数据，然后然后这个数据的对象
        add_obj = this.site_list.find(item => {
          // console.log(this.site_list);
          // console.log(element);
          // console.log(item.address);
          return element === item.address;
        });
        // 4.将对象中的id，添加到列表
        add_list.push(add_obj.id);
        // console.log(add_obj);
      });
      // console.log(add_list);
      // 6.将ID列表 set到site数据中，因为select是同value值
      this.$set(this.form, "site", add_list);
    },
    // 修改用户数据
    putUser() {
      var _fromData = {
        level: this.form.level,
        site: this.form.site,
        user: this.form.user,
        password: this.form.password,
        email: this.form.email,
        phone: this.form.phone
      };
      // console.log(_fromData);
      onModify_user_put(_fromData).then(data => {
        alert(data.data);
        this.reload();
        // 使用jquery代替bootstrap的 data-dismiss="modal"方法 执行完后，关闭模态框和遮罩层
        $("#modifyUser").modal("hide");
      });
    },
    // 修改站点数据时显示旧的数据(友好体验)
    onModify_add(add) {
      // 1.先把所有数据渲染上去
      this.form = add;
      // 5.得到用户id列表
      let users_list = [];
      // console.log(add.addUser);
      // 2.遍历数组
      add.addUser.forEach(element => {
        let user_obj = [];
        // 3.再遍历对象中，和另外的数组作对比，返回数据对象
        user_obj = this.user_list.find(item => {
          // console.log(item);
          return element === item.user;
        });
        // 4.将用户对象的ID添加到列表
        // console.log(user_obj);
        users_list.push(user_obj.id);
      });
      this.$set(this.form, "addUser", users_list);
    },
    // 修改站点数据
    putAddress() {
      var _fromData = {
        addUser: this.form.addUser,
        address: this.form.address,
        ip: this.form.ip,
        add_email: this.form.add_email,
        add_phone: this.form.add_phone
      };
      // console.log(_fromData);
      onModify_address_put(_fromData).then(data => {
        alert(data.data);
        this.reload();
        // 使用jquery代替bootstrap的 data-dismiss="modal"方法 执行完后，关闭模态框和遮罩层
        $("#myModalAddress").modal("hide");
      });
    },
    // 查询告警配置信息
    get_addressconfig() {
      // let ip_address = { ip_address: this.ip_address };
      Warning_config_post().then(data => {
        if (data.data !== undefined && data.data.length > 0) {
          this.warning_list = data.data[0];
        } else {
          return null;
        }
        // console.log(data.data);
        // console.log(this.warning_list);
      });
    },
    // 站点配置
    putAddressConfig() {
      var _fromData = {
        server_address: this.warning_list.server_address,
        port_config: this.warning_list.port_config,
        send_mail: this.warning_list.send_mail,
        password: this.warning_list.password,
        COM_phone: this.warning_list.COM_phone,
        other_config: this.warning_list.other_config
      };
      let clone_fromData = _.cloneDeep(_fromData);
      console.log(clone_fromData);
      Warning_config_put(clone_fromData).then(data => {
        alert(data.data);
        this.reload();
        // 使用jquery代替bootstrap的 data-dismiss="modal"方法 执行完后，关闭模态框和遮罩层
        $("#myModalAddressConfig").modal("hide");
      });
    }
  }
};
</script>


<style scoped>
/* scoped代表只在当前组件中有效 */
</style>


