<template>
  <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
    <!-- white-space: nowrap  规定段落中的文本不进行换行 -->
    <!-- -webkit-overflow-scrolling:touch;使用具有回弹效果的滚动, 当手指从触摸屏上移开，内容会继续保持一段时间的滚动效果。继续滚动的速度和持续的时间和滚动手势的强烈程度成正比。同时也会创建一个新的堆栈上下文。 -->
    <div
      style=" font-size:18px; color:#0000FF; height:40px; white-space: nowrap; line-height:40px; overflow-x: auto;-webkit-overflow-scrolling:touch; "
    >
      <span>{{$t('bar.Site')}}：{{ this.add_list.add_name}}&nbsp;&nbsp;&nbsp;&nbsp;{{$t('bar.principal')}}：{{ this.add_list.username[0].username}}&nbsp;&nbsp;&nbsp;&nbsp;{{$t('bar.phone')}}：{{ this.add_list.mobile[0].mobile}}&nbsp;&nbsp;&nbsp;&nbsp;{{$t('bar.mailbox')}}：{{ this.add_list.email[0].email}}</span>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { Bar_get } from "../Api/api";
import { constants } from "fs";
export default {
  data() {
    return {
      add_list: {
        add_name: "",
        username: [{ username: "" }],
        mobile: [{ mobile: "" }],
        email: [{ email: "" }]
      }
    };
  },
  created() {
    this.bar_data();
  },
  methods: {
    bar_data() {
      // get_bar_list通过mapGetters解包得到
      let params = { params: { ip: this.get_bar_list } };
      // console.log(params);
      Bar_get(params).then(data => {
        // console.log(typeof data.data);
        // this.add_list = JSON.parse(data.data);
        this.add_list = data.data;
        // console.log(this.add_list)

      });
    }
  },
  computed: {
    ...mapGetters(["get_bar_list"])
  }
};
</script>
<style>
</style>



