
export default {
    init() {
        console.log("初始化bing地图脚本...");
        // bing map key
        const bingUesrKey = 'Aqcwj7BiTxT9aSixmXgEAqP8GmZlRVSSGJLjowkE-oxH15bEMD1x5P6PsdAXk5wS&callback=loadMapScenario';
        const BingMap_URL = 'http://www.bing.com/api/maps/mapcontrol?callback=GetMap&key=' + bingUesrKey;
        // const BingMap_URL = 'http://www.bing.com/api/maps/mapcontrol?'
        // Promise 异步耗时操作
        return new Promise((resolve, reject) => {
            if (typeof Microsoft !== "undefined") {
                resolve(Microsoft);
                return true;
            }
            // 插入script脚本   document.createElement() 方法通过指定名称创建一个元素
            // 创建一个名称为script的脚本
            var scriptNode = document.createElement("script");

            scriptNode.setAttribute("type", "text/javascript");
            scriptNode.setAttribute("src", BingMap_URL);
            document.body.appendChild(scriptNode);

            // 等待页面加载完毕回调
            var timeout = 0;
            var interval = setInterval(() => {
                // 超时10秒加载失败
                if (timeout >= 20) {
                    reject();
                    clearInterval(interval);
                    console.error("bing地图脚本初始化失败...");
                }
                // 加载成功
                if (typeof Microsoft !== "undefined") {
                    resolve(Microsoft);
                    clearInterval(interval);
                    console.log("bing地图脚本初始化成功...");
                }
                timeout += 1;
            }, 500);
        });
    }
}



