<template>
  <div id="app" class="container">
    <!-- 导航栏 -->
    <glo></glo>
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
      <span align="center">
        <h3>{{$t('details.Alarm_list')}}</h3>
      </span>
      <div class="table-responsive" style="max-height:150px;overflow-y:auto;">
        <table class="table table-bordered table-hover">
          <thead>
            <tr>
              <th>{{$t('details.Alarm_level')}}</th>
              <th>{{$t('details.Alarm_device')}}</th>
              <th>{{$t('details.Alarm_name')}}</th>
              <th>{{$t('details.Alarm_content')}}</th>
              <th>{{$t('details.Alarm_time')}}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in warning_list3" :key="index">
              <td>{{ item.alarm_level }}</td>
              <td>{{ item.alarm_model }}</td>
              <td>{{ item.alarm_divice }}</td>
              <td>{{ item.alarm_data }}</td>
              <td>{{ item.alarm_time }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
      <router-link
        :to="{name:'Equipment',query:{id:this.$route.query.id,name:this.$route.query.address}}"
      >
        <div
          style="height:100px; width:100%; border:3px solid;"
          onmouseover="this.style.cursor='pointer'"
        >
          <div
            style="background:lightblue; height:30px; line-height:30px;text-align:center;"
          >{{$t('details.surroundings')}}</div>
          <div style="margin-left:30px;margin-top:10px;text-align:center;">
            {{$t('details.Supply_air_temperature')}}：{{warning_list2.AC_SAT}}℃&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{$t('details.Return_air_temperature')}}：{{warning_list2.AC_RAT}}℃
            <br />
            {{$t('details.Return_air_humidity')}}：{{warning_list2.AC_RAH}}%&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{$t('details.Air_conditioning_status')}}：{{warning_list2.AC_ST}}
            <br />
          </div>
        </div>
      </router-link>
      <!-- -------------------------------------------------------- -->
      <br />
      <router-link
        :to="{name:'Equipment',query:{id:this.$route.query.id,name:this.$route.query.address}}"
      >
        <div
          style="height:100px; width:100%; border:3px solid;"
          onmouseover="this.style.cursor='pointer'"
        >
          <div
            style="background:lightblue; height:30px; line-height:30px; text-align:center;"
          >{{$t('details.Security')}}</div>
          <div style="margin-left:30px;margin-top:10px;text-align:center;">
            {{$t('details.Door_status')}}：{{warning_list2.DR_ST}}
            <br />
            {{$t('details.video')}}：{{this.ip_address}}
            <br />
          </div>
        </div>
      </router-link>
      <!-- -------------------------------------------------------- -->
    </div>
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
      <!-- -------------------------------------------------------- -->
      <router-link
        :to="{name:'Equipment',query:{id:this.$route.query.id,name:this.$route.query.address}}"
      >
        <div
          style="height:100px; width:100%; border:3px solid;"
          onmouseover="this.style.cursor='pointer'"
          onclick="document.location='#/equipment'"
        >
          <div
            style="background:lightblue; height:30px; line-height:30px;text-align:center;"
          >{{$t('details.electric_power')}}</div>
          <div style="margin-left:30px;margin-top:10px;text-align:center;">
            {{$t('details.Mains_voltage')}}：{{warning_list2.PW_MV}}V&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{$t('details.The_output_voltage')}}：{{warning_list2.PW_OV}}V
            <br />
            {{$t('details.Load_rate')}}：{{warning_list2.PW_LR}}%&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{$t('details.Backup_time')}}：{{warning_list2.PW_BT}}H
            <br />
            {{$t('details.Total_power')}}：{{warning_list2.PW_ROW}}KW&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{$t('details.Total_energy_consumption')}}：{{warning_list2.PW_EC}}KW/H
            <br />
          </div>
        </div>
      </router-link>
      <!-- -------------------------------------------------------- -->
      <br />
      <router-link
        :to="{name:'Equipment',query:{id:this.$route.query.id,name:this.$route.query.address}}"
      >
        <div
          style="height:100px; width:100%; border:3px solid;"
          onmouseover="this.style.cursor='pointer'"
        >
          <div
            style="background:lightblue; height:30px; line-height:30px; text-align:center;"
          >{{$t('details.other')}}</div>
          <div style="margin-left:30px;margin-top:10px;text-align:center;">
            {{$t('details.Water_leakage')}}：{{warning_list2.DR_MW}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{$t('details.Smoke_state')}}：{{warning_list2.DR_SD}}
            <br />
            {{$t('details.Other_state')}}：
            <br />
          </div>
        </div>
      </router-link>
      <!-- -------------------------------------------------------- -->
    </div>
    <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6">
      <router-link :to="{name:'HistoryData',query:{ip:this.ip_address,name:this.name}}">
        <div
          style="background:pink; height:40px; line-height:40px; text-align:center; border:3px solid; border-bottom0; font-size:20px"
          onmouseover="this.style.cursor='pointer'"
        >{{$t('details.Historical_data')}}</div>
      </router-link>
      <!-- -------------------------------------------------------- -->
    </div>
    <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6">
      <router-link :to="{name:'HistoryWarning',query:{ip:this.ip_address,name:this.name}}">
        <div
          style="background:pink; height:40px; line-height:40px; text-align:center; border:3px solid; border-bottom0; font-size:20px"
          onmouseover="this.style.cursor='pointer'"
        >{{$t('details.Historical_alarm')}}</div>
      </router-link>
      <!-- -------------------------------------------------------- -->
    </div>
    <!-- 底部信息栏 -->
    <globar></globar>
  </div>
</template>
<script>
import { warninglist } from "../Api/api";
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import Axios from "axios";
export default {
  data() {
    return {
      name: "",
      ip_address: "",

      warning_list3: {},
      warning_list2: {},
      warning_list1: {}
    };
  },
  created() {
    // console.log(this.$route.query);
    // 接受传过来的参数，并赋值，再次进行传递
    this.ip_address = this.$route.query.ip;
    this.name = this.$route.query.name;
    // 将ip地址存到vuex然后传给bar
    this.$store.commit("bar_list", this.ip_address);
  },
  methods: {
    get_test() {
      axios
        .get("http://192.168.1.200:8080/getdata")
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  computed: {
    ...mapGetters(["get_showTimeList3"]),
    ...mapGetters(["get_showTimeList2"])
    // ...mapGetters(["get_showTimeList1"])
  },
  watch: {
    get_showTimeList3(val) {
      this.warning_list3 = val;
      // console.log(this.warning_list3)
    },
    get_showTimeList2(val) {
      this.warning_list2 = val;
    }
    // get_showTimeList1(val) {
    //   this.warning_list1 = val;
    // }
  }
};
</script >
<style >
</style>



