import Vue from 'vue'
import Router from 'vue-router'

import Main from '@/components/Main'
import Login from '@/components/Login'
import Manage from '@/components/Management'
import Test from '@/components/test'
import Details from '@/components/Details'
import Equipment from '@/components/Equipment_details'
import HistoryData from '@/components/Historical_data'
import HistoryWarning from '@/components/HistoryWarning'
import VuexTest from '@/components/VuexTest'
import Mshow from '@/components/Mshow'
import BingMap from '@/components/BingMap'
import BaiduMap from '@/components/BaiduMap'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/main',
      name: 'Main',
      component: Main
    },
    {
      path: '/manage',
      name: 'Manage',
      component: Manage
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/details',
      name: 'Details',
      component: Details
    },
    {
      path: '/equipment',
      name: 'Equipment',
      component: Equipment
    },
    {
      path: '/historyData',
      name: 'HistoryData',
      component: HistoryData
    },
    {
      path: '/warning',
      name: 'HistoryWarning',
      component: HistoryWarning
    },
    {
      path: '/vuextest',
      name: 'VuexTest',
      component: VuexTest
    },
    {
      path: '/mshow',
      name: 'Mshow',
      component: Mshow
    },
    {
      path: '/bingmap',
      name: 'BingMap',
      component: BingMap
    },
    {
      path: '/BaiduMap',
      name: 'BaiduMap',
      component: BaiduMap
    }
  ]
})



