<template>
  <div>
    <div id="map" ref="rootmap"></div>
    <select id="layer-select">
      <option value="Aerial">Aerial</option>
      <option value="AerialWithLabels" selected>Aerial with labels</option>
      <option value="Road">Road (static)</option>
      <option value="RoadOnDemand">Road (dynamic)</option>
      <option value="collinsBart">Collins Bart</option>
      <option value="ordnanceSurvey">Ordnance Survey</option>
    </select>
  </div>
</template>

<script>
import "ol/ol.css";
import View from "ol/View";
import Map from "ol/Map";
import mapconfig from "../config/mapconfig";
export default {
  data() {
    return {
      map: null
    };
  },
  mounted() {
    // 获取dom元素
    var mapcontainer = this.$refs.rootmap;

    this.map = new Map({
      // 地图交互或者动画过程
      loadTilesWhileInteracting: true,
      // 让id为map的div作为地图的容器
      // target: "map",
      // mapcontainer的div作为地图的容器
      target: mapcontainer,
      // 设置地图图层 导入的地图配置信息
      layers: mapconfig.streetmap(),
      // 设置显示地图的视图
      view: new View({
        // transform（） 把EPSG:4326的坐标，转换为EPSG:3857坐标，因为ol默认使用的是EPSG:3857坐标
        // center: ol.proj.transform([104.06, 30.67], "EPSG:4326", "EPSG:3857"),

        // 目前OpenLayers 3支持两种投影，一个是EPSG:4326，等同于WGS84坐标系，另一个是EPSG:3857，等同于900913，
        // 由Mercator投影而来，经常用于web地图，参见详情。一个是全球通用的，一个是web地图专用的

        // 指定投影使用EPSG:4326
        // projection: "EPSG:4326",
        // 定义地图显示中心于经度，纬度
        center: [mapconfig.x, mapconfig.y],
        // 并且定义地图显示层级为zoom
        zoom: mapconfig.zoom
      })
    });
    var select = document.getElementById("layer-select");
    function onChange() {
      var layers = mapconfig.streetmap().layers;
      console.log(layers)
      var style = select.value;
      for (var i = 0, ii = layers.length; i < ii; ++i) {
        layers[i].setVisible(styles[i] === style);
      }
    }
    select.addEventListener("change", onChange);
    onChange();
  }
};
</script>

<style>
#map {
  height: 100%;
}
/*隐藏ol的一些自带元素*/
.ol-attribution,
.ol-zoom {
  display: none;
}
</style>




