<template>
  <div class="map-container">
    <input id="searchTbx" type="text" />
    <input type="button" value="Search" @click="Search()" />
    <br />
    <div id="myMap" style="position:relative;width:600px;height:400px;float:left;"></div>
    <div id="output" style="margin-left:10px;float:left;"></div>
    <!-- <div style="width:500px;height:500px;float:right;"> -->
    <!-- 动态图标 -->
    <div class="circle_box">
      <div class="round">
        <div class="circle">
          <a>ATTOM</a>
        </div>
        <div class="circle_bottom animation"></div>
        <div class="circle_bottom2 animation2"></div>
      </div>
      <!-- </div> -->
    </div>

    <!-- <div id="myMap"></div> -->
  </div>
</template>

<script>
import initBingMap from "../config/initMap";
import { constants } from "fs";
export default {
  data() {
    return {
      lngNum: null, // 经度
      latNum: null, // 纬度
      map: "",
      searchManager: "",
      searchRequest: ""
    };
  },
  created() {},
  mounted() {
    initBingMap.init().then(Microsoft => {
      console.log(Microsoft);
      // console.log("加载成功...");
      this.GetMap();
      // console.log("初始化成功...");
    });
  },
  methods: {
    GetMap() {
      const bingUesrKey =
        "Aqcwj7BiTxT9aSixmXgEAqP8GmZlRVSSGJLjowkE-oxH15bEMD1x5P6PsdAXk5wS&callback=loadMapScenario";
      this.map = new Microsoft.Maps.Map(document.getElementById("myMap"), {
        credentials: bingUesrKey,
        center: new Microsoft.Maps.Location(35.027222, -111.0225),
        mapTypeId: Microsoft.Maps.MapTypeId.aerial,
        zoom: 3
      });
      // getCenter()获取坐标点
      var center = this.map.getCenter();
      // {latitude: 35.027221999999995, longitude: -111.0225, altitude: 0, altitudeReference: -1}
      // console.log(center)
      var pin = new Microsoft.Maps.Pushpin(center, {
        icon: "https://www.bingmapsportal.com/Content/images/poi_custom.png",
        anchor: new Microsoft.Maps.Point(12, 39),
        title: "站点",
        subTitle: "上海"
        // text: "motta",
        // color: "red"
      });
      this.map.entities.push(pin);
      //将视图更改事件添加到映射中。
    },
    Search() {
      // console.log(this.map.entities);
      if (!this.searchManager) {
        // 创建搜索管理器的实例并执行搜索
        Microsoft.Maps.loadModule("Microsoft.Maps.Search", () => {
          this.searchManager = new Microsoft.Maps.Search.SearchManager(
            this.map
          );
          this.Search();
        });
      } else {
        // 从地图中删除以前的所有结果
        // console.log(this.map);
        this.map.entities.clear();
        // console.log(this.map.entities);
        // 获取用户查询并对其进行地理编码
        var query = document.getElementById("searchTbx").value;
        this.geocodeQuery(query);
      }
    },
    geocodeQuery(query) {
      var _map = this.map;
      // console.log(this.map);
      this.searchRequest = {
        where: query,
        callback(r) {
          // console.log(r);
          // console.log(r.results.length);
          if (r && r.results && r.results.length > 0) {
            var pin,
              pins = [],
              locs = [],
              output = "Results:<br/>";

            for (var i = 0; i < r.results.length; i++) {
              //为每个结果创建一个图钉
              // console.log(r.results[i].location);
              pin = new Microsoft.Maps.Pushpin(r.results[i].location, {
                // text: i + ""
                icon:
                  "https://www.bingmapsportal.com/Content/images/poi_custom.png",
                anchor: new Microsoft.Maps.Point(12, 39)
              });
              pins.push(pin);
              locs.push(r.results[i].location);
              output += i + ") " + r.results[i].name + "<br/>";
            }
            // console.log(pins);
            // console.log(output);
            //将引脚添加到地图中
            console.log(_map);
            // console.log(this.map + "  map未定义？");
            _map.entities.push(pins);
            //显示结果列表
            document.getElementById("output").innerHTML = output;
            //确定边界框以最佳地查看结果
            var bounds;
            if (r.results.length == 1) {
              bounds = r.results[0].bestView;
            } else {
              //使用结果中的位置计算边界框。
              bounds = Microsoft.Maps.LocationRect.fromLocations(locs);
            }
            _map.setView({ bounds: bounds });
          }
        },
        errorCallback(e) {
          //如果出现错误，请提醒用户注意。
          alert("未找到结果。");
          // alert("No results found.");
        }
      };
      //发出地理编码请求。
      this.searchManager.geocode(this.searchRequest);
    }
  }
};
</script>

<style scoped>
#myMap {
  width: 100%;
  height: 400px;
  border: 1px solid #000;
}

.circle_box {
  float: center;
  width: 100px;
  /* margin: 50px auto; */
  height: 100px;
  position: relative;
}

.round:nth-child(1) {
  position: absolute;
  padding: 13px;
  left: 0px;
  top: 79px;
}

.round:nth-child(1) div {
  background: #3a96e1;
}

.circle a {
  font-size: 12px;
  line-height: 100px;
  color: #000735;
  font-weight: bold;
}

.circle {
  background: #3a96e1;
  border-radius: 100%;
  width: 30px;
  height: 30px;
  z-index: 999;
  text-align: center;
  top: 18px;
  left: 18px;
  position: absolute;
}
.circle_bottom {
  background: rgba(58, 150, 225, 0.4);
  border-radius: 100%;
  width: 40px;
  height: 40px;
  filter: alpha(opacity=40);
  z-index: 998;
  position: absolute;
  top: 18px;
  left: 18px;
}

.circle_bottom2 {
  background: rgba(58, 150, 225, 0.2);
  border-radius: 100%;
  width: 50px;
  height: 50px;
  filter: alpha(opacity=20);
  z-index: 997;
  position: relative;
}

.animation {
  -webkit-animation: twinkling 2.1s infinite ease-in-out;
  animation: twinkling 2.1s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.animation2 {
  -webkit-animation: twinkling 2.1s infinite ease-in-out;
  animation: twinkling 2.1s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
/* 
目前浏览器都不支持 @keyframes 规则。
Firefox 支持替代的 @-moz-keyframes 规则。
Opera 支持替代的 @-o-keyframes 规则。
Safari 和 Chrome 支持替代的 @-webkit-keyframes 规则。
 */
@-webkit-keyframes twinkling {
  0% {
    opacity: 0.2;
    filter: alpha(opacity=20);
    -webkit-transform: scale(1);
  }

  50% {
    opacity: 0.5;
    filter: alpha(opacity=50);
    -webkit-transform: scale(1.12);
  }

  100% {
    opacity: 0.2;
    filter: alpha(opacity=20);
    -webkit-transform: scale(1);
  }
}

@keyframes twinkling {
  0% {
    opacity: 0.2;
    filter: alpha(opacity=20);
    -webkit-transform: scale(1);
  }

  50% {
    opacity: 0.5;
    filter: alpha(opacity=50);
    -webkit-transform: scale(1.12);
  }

  100% {
    opacity: 0.2;
    filter: alpha(opacity=20);
    -webkit-transform: scale(1);
  }
}
</style>


