<template>
  <div>
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
      <div ref="myEchart" style="width: 500px; height:500px; border:1px solid"></div>
    </div>
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
      <div ref="myEchart01" style="width:500px;height:500px; border:1px solid"></div>
    </div>
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
      <div ref="myEchart02" style="width:500px;height:500px; border:1px solid"></div>
    </div>
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
      <div class="panel-loading-icon" v-show="showLoadingPanel[3]">
        <div
          class="tendency panel_content_wrap"
          style="width: 100%"
          ref="chartBoxRef"
          id="ptrendDiv"
        >
          <gloecharts :lineData="lineData" :isClick="true" @clickFn="trendChartClick"></gloecharts>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import echarts from "echarts";
export default {
  data() {
    return {
      lineData: {
        // 传递给子组件的 数据 + 宽高
        options: {},
        divOffset: {
          eWidth: "",
          eHeight: ""
        }
      },
      // -------------------------------------------
      charts: "",
      charts1: "",
      charts2: ""
    };
  },
  mounted() {
    //----在mounted中 使用window.onresize函数     （_throttle函数是节流函数）
    // 如果将水龙头拧紧直到水是以水滴的形式流出，那你会发现每隔一段时间，就会有一滴水流出。也就是会说预先设定一个执行周期，当调用动作的时刻大于等于执行周期则执行该动作，然后进入下一个新周期。接口定义：

    // 频率控制 返回函数连续调用时，action 执行频率限定为 次 / delay
    // * @param delay  {number}    延迟时间，单位毫秒
    // * @param action {function}  请求关联函数，实际应用需要调用的函数
    // * @return {function}    返回客户调用函数
    // throttle(delay, action);
    this.$nextTick(() => {
      window.onresize = _throttle(() => {
        //Vue.set( target, propertyName/index, value )参数：
        // {Object | Array} target
        // {string | number} propertyName/index
        // {any} value
        // 返回值：设置的值。
        // 用法：向响应式对象中添加一个属性，并确保这个新属性同样是响应式的，且触发视图更新。它必须用于向响应式对象上添加新属性，因为 Vue 无法探测普通的新增属性 (比如 this.myObject.newProperty = 'hi')
        // 注意对象不能是 Vue 实例，或者 Vue 实例的根数据对象。
        Vue.set(
          this.lineData.divOffset,
          "eWidth",
          this.$refs.chartBoxRef.offsetWidth - 15 + "px"
        );
        Vue.set(
          this.lineData.divOffset,
          "eHeight",
          this.$refs.chartBoxRef.offsetHeight - 25 + "px"
        );
      }, 200);
    });
    axios.get(url).then(res => {
      Vue.set(this.lineData, "options", { echarts数据 });
      Vue.set(
        this.lineData.divOffset,
        "eWidth",
        this.$refs.chartBoxRef.offsetWidth - 15 + "px"
      );
      Vue.set(
        this.lineData.divOffset,
        "eHeight",
        this.$refs.chartBoxRef.offsetHeight - 25 + "px"
      );
    });
    // --------------------- ----------------------------------
    this.initechart();
  },
  methods: {
    initechart() {
      this.charts = echarts.init(this.$refs.myEchart, "light");
      this.charts1 = echarts.init(this.$refs.myEchart01, "dark");
      this.charts2 = echarts.init(this.$refs.myEchart02, "dark");
      var base = +new Date(2014, 9, 3);
      var oneDay = 24 * 3600 * 1000;
      var date = [];
      var data = [Math.random() * 150];
      var now = new Date(base);
      function addData(shift) {
        now = [now.getFullYear(), now.getMonth() + 1, now.getDate()].join("/");
        date.push(now);
        data.push((Math.random() - 0.4) * 10 + data[data.length - 1]);

        if (shift) {
          date.shift();
          data.shift();
        }

        now = new Date(+new Date(now) + oneDay);
      }
      for (var i = 1; i < 100; i++) {
        addData();
      }
      // 准备图表
      this.charts.setOption({
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: date
        },
        yAxis: {
          boundaryGap: [0, "50%"],
          type: "value"
        },
        series: [
          {
            name: "成交",
            type: "line",
            smooth: true,
            symbol: "none",
            stack: "a",
            areaStyle: {
              normal: {}
            },
            data: data
          }
        ]
      });
      var _this = this; // 在ES6语法前  定义this要定义在函数外面或者改成箭头函数
      this.charts.showLoading();
      setInterval(function() {
        addData(true);
        _this.charts.hideLoading();
        _this.charts.setOption({
          xAxis: {
            data: date
          },
          series: [
            {
              name: "成交",
              data: data
            }
          ]
        });
      }, 500);
      // ---------------柱状图--------------------
      this.charts1.setOption({
        title: {
          text: "异步数据加载示例"
        },
        tooltip: {},
        legend: {
          data: ["销量"]
        },
        xAxis: {
          data: []
        },
        yAxis: {},
        series: [
          {
            name: "销量",
            type: "bar",
            data: []
          }
        ]
      });
      // ECharts 默认有提供了一个简单的加载动画。只需要调用 showLoading 方法显示。数据加载完成后再调用 hideLoading 方法隐藏加载动画。
      this.charts1.showLoading();
      this.$axios.get("http://localhost:8080/static/001.json").then(res => {
        this.charts1.hideLoading();

        this.charts1.setOption({
          xAxis: {
            data: res.data.categories
          },
          series: [
            {
              name: "销量",
              data: res.data.data
            }
          ]
        });
      });
      // -------------折线图-------------
      this.charts2.setOption({
        title: {
          text: "折线图堆叠"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["邮件营销", "联盟广告", "视频广告", "直接访问", "搜索引擎"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "邮件营销",
            type: "line",
            stack: "总量",
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: "联盟广告",
            type: "line",
            stack: "总量",
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: "视频广告",
            type: "line",
            stack: "总量",
            data: [150, 232, 201, 154, 190, 330, 410]
          },
          {
            name: "直接访问",
            type: "line",
            stack: "总量",
            data: [320, 332, 301, 334, 390, 330, 320]
          },
          {
            name: "搜索引擎",
            type: "line",
            stack: "总量",
            data: [820, 932, 901, 934, 1290, 1330, 1320]
          }
        ]
      });
    }
  }
};
// this.$axios
//   .get("http://localhost:8080/static/purple-passion.json")
//   .then(res => {
//     let data = res.data;
//     console.log("1" + data);
//     // obj = JSON.parse(data);  // 将字符串转成对象 已经是对象了
//     // 注册echart的主题
//     echarts.registerTheme("purple-passion", data);
//     // return (this.result = data);
//   });
// async function comput() {
//   let x = await this.$axios.get();
//   this.result = x;
//   console.log("3" + this.result);
// }
</script>
<style>
</style>


      


